body{
  background-image: url("https://wallpaperplay.com/walls/full/e/5/e/42053.jpg");
}

.App{
  background: #094b63;
  min-height: 700px;
  width: 400px;
  margin: 40px auto;
  border-radius: 20px;
  text-shadow: navy;
}

.App h1{
  font-size: 50px;
  font-family: Kefa;
  color: #ffffff11;
  padding: 0 110px;
  height: 20px;
}

#to-do-form input {
  background-color: #095a71;
  border: 0;
  margin: 20px;
  height: 40px;
  width: 200px;
  padding: 0 20px;
  font-size: 17px;
  border-radius: 20px;
  color: #f0f8ff77;
  cursor: text;
}

#to-do-form input::placeholder{
  color: #F0F8FF55;
}

#to-do-form input:focus{
  outline: none;
}

#to-do-form button{
  height: 40px;
  width: 80px;
  background-color: #095a71;
  border-radius: 20px;
  border: 0;
  font-size: 20px;
  color: aliceblue;
  cursor: pointer;
  font-weight: bolder;
}

#to-do-form button:focus{
  outline: none;
}